@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);

body {
  font-family: 'Open Sans', sans-serif;
}

.search-image-widget {
  padding: 20px;
}

.wrap {
  width: 20%;
}

.search-form {
  //   width: 100%;
  //   position: relative;
  display: flex;
}

.search-form__input {
  width: 100%;
  border: 3px solid #00b4cc;
  border-right: none;
  padding: 5px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
  font-size: 97%;
}

.search-form__input:focus {
  color: #000000;
}

.search-form__btn {
  width: 40px;
  height: 36px;
  border: 1px solid #00b4cc;
  background: #00b4cc;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

